<!-- 手机短信登录页 -->
<template>
	<view class="loginBox">
		<!-- 头部导航栏 -->
		<NavBar title='登录'></NavBar>
		<view class="logo-box">
			<image src='../../static/userLogo.png'></image>
			<!-- <view class="title">神领快递</view> -->
		</view>

		<view class="open-dialog" @click="handleOpen">微信登录</view>

		<!-- 服务条款及隐私政策弹窗 -->
		<uni-popup ref="popup" type="bottom" >
			<view class="header">
				<view class="title">服务条款及隐私政策</view>
				<image src='../../static/guanbi.png' @click="handleClose"></image>
			</view>
			<view class="content">
				<view>
					在您注册成为神领快递会员的过程中您需要通过点击同意的形式在线签署<text>《神领快递服务条款》</text>、<text>《神领快递隐私政策</text>》请您务必仔细阅读充分理解条款内容后再点击同意尤其是以粗体并下划线标识的条款因为这些条款可能会明确您应履行的义务或对您的权利有所限制。
				</view>
				<view class="tips">
					请您注意：如果您不同意上述服务条款隐私政策或其中任何约定请您停止注册
				</view>
			</view>
			<view class="footer">
				<button class="cancel-btn btn" @click="handleClose">不同意</button>
				<button class="agree-btn btn" open-type="getPhoneNumber" @getphonenumber="decryptPhoneNumber">同意</button>
			</view>
		</uni-popup>
	</view>
</template>

<script setup>
	import {
		ref,
		reactive,
		onMounted
	} from 'vue';
	import {
		useStore
	} from 'vuex';
	// 接口
	import {
		login
	} from '../api/login.js';
	// 验证
	import {
		validatePhone,
		timeCountdown,
		isPhone
	} from '@/utils/validate';
	import NavBar from '@/components/Navbar/index.vue'
	// ------定义变量------
	const store = useStore(); //vuex获取储存数据
	const popup = ref();
	const handleOpen = () => {
		popup.value.open()
	}
	const decryptPhoneNumber = (e)=>{
		handleClose()
		wx.login({
			success(res){
				console.log(e,res,'eeeeee')
				login({
					code:res.code,
					phoneCode:e.detail.code
				}).then((res)=>{
					console.log(res,'res')
					//将token存到缓存中，后续在统一请求头上加上token
					uni.setStorageSync('token', res.data.token);
					//登录成功后跳转到首页
					uni.navigateTo({
						url: '/pages/index/index'
					});
				})
			}
		})
		
	}
	
	const handleClose = ()=>{
		popup.value.close()
	}
	// ------生命周期------
	onMounted(() => {
		console.log(uni.getStorageSync('token'),'1111')
	});
</script>
<style src="./index.scss" lang="scss" scoped></style>
